@import url('https://fonts.googleapis.com/css2?family=Audiowide&display=swap');
.box {
    position: relative;
    width: 300px;
    height: 300px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 15px;
    background: #060c21; /* Change */
    transition: .5s;
    
}
@media screen and (max-width: 767px) {
    .PanelistContainer {
        padding-top: 20vh;
    }
}

.box:hover {
    height: 400px;
}

.PanelistContainer {
    padding-top: 3vh;
    font-size: 300%;
    font-weight: 600;
    text-align: center;
    color: aliceblue;
    text-shadow: 0px 3px 15px #5ea0d9;
}
@media screen and (max-width: 1023px) {
    .PanelistContainer {
        padding-top: 17vh;
        font-size: 300%;
        font-weight: 600;
        text-align: center;
        color: aliceblue;
        text-shadow: 0px 3px 15px #5ea0d9;
    }
}
.box .imgBx {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    padding: 10px;
    box-sizing: border-box;
}

.box .imgBx img {
    max-width: 100%;
    opacity: .3;
    transition: .5s; 
}

.box:hover .imgBx img {
    opacity: 1;  
}

.box:before {
    content: "";
    position: absolute;
    top: -2px;
    left: -2px;
    right: -2px;
    bottom: -2px;
    background: #fff;
    z-index: -1;

}

.box:after {
    content: "";
    position: absolute;
    top: -2px;
    left: -2px;
    right: -2px;
    bottom: -2px;
    background: #fff;
    z-index: -2;
    filter: blur(40px);

}

.box:before,
.box:after   {  
    background: linear-gradient(235deg, #2196f3, #010615, #F00000);
}

.box:nth-child(2):before,
.box:nth-child(2):after   { /* not supported ðŸ˜£ */
    background: linear-gradient(235deg, #2196f3, #010615, #F00000);
}

.box:nth-child(3):before,
.box:nth-child(3):after   { /* not supported ðŸ˜£ */
    background: linear-gradient(235deg, #772aff, #010615, #2196f3);
}

.box .content {
    position: absolute;
    bottom: 0;
    left: 10px;
    right: 10px;
    bottom: 10px;
    height: 90px;
    background: rgba(0,0,0,.04);
    display: flex;
    justify-content: center;
    align-items: center;
    opacity: 0;
    transition: 0.5s;
}

.box:hover .content {
    opacity: 1;
}

.box .content h2 {
    font-size: 20px;
    color: #fff;
    font-weight: 500; /* just one style ðŸ˜ */
    line-height: 20px;
    letter-spacing: 1px;
    text-transform: uppercase;
}

.box .content h2 span {
    font-size: 14px;
    color: #fff;
    font-weight: 200; /* just one style ðŸ˜ */
    
    letter-spacing: 2px;
}