.fade {
    position: relative;
    width: 100%;
    min-height: 60vh;
    top: -25px;
    background-image: linear-gradient(0deg, transparent, black 75%);
    z-index: 1;
  }
  
  .star-wars {
    top: 50px;
    display: flex;
    justify-content: center;
    position: relative;
    height: 800px;
    color: #feda4a;
    font-family: 'Pathway Gothic One', sans-serif;
    font-size: 6vw;
    font-weight: 600;
    letter-spacing: 6px;
    line-height: 150%;
    perspective: 400px;
    text-align: center;
    overflow: hidden;
  }
  
  .crawl-col {
      overflow: hidden;
      height: 100vh;
  }

  .crawl {
    position: relative;
    top: 99999px;
    transform-origin: 50% 100%;
    animation: crawl 60s linear infinite;
  }
  
  .crawl > .title {
    font-size: 6vw;
    text-align: center;
  }
  
  .crawl > .title h1 {
    margin: 0 0 100px;
    font-size: 15vw;
    font-weight: 600;
    text-transform: uppercase;
  }
  
  @keyframes crawl {
      0% {
      top: -100px;
      transform: rotateX(20deg)  translateZ(0);
    }
      100% { 
      top: -6000px;
      transform: rotateX(25deg) translateZ(-2500px);
    }
  }

  .card-container {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    padding: 40px 0;
}
.card-container .card-box {
    position: relative;
    width: 320px;
    height: 400px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 40px 30px;
    transition: 0.5s;
}
.card-container .card-box:before {
    content: '';
    position: absolute;
    top: 0;
    left: 50px;
    width: 50%;
    height: 100%;
    background: #fff;
    border-radius: 8px;
    transform: skewX(15deg);
    transition: 0.5s;
}
.card-container .card-box:after {
    content: '';
    position: absolute;
    bottom: 0;
    left: 50px;
    width: 50%;
    height: 100%;
    background: #fff;
    border-radius: 8px;
    transform: skewX(15deg);
    filter: blur(30px);
    transition: 0.5s;
}
.card-container .card-box:hover:before,
.card-container .card-box:hover:after {
    transform: skewX(0deg);
    left: 20px;
    width: calc(100% - 90px);
}
.card-container .card-box:nth-child(1):before,
.card-container .card-box:nth-child(1):after {
    background: linear-gradient(315deg, #ffbc00, #ff0058);
}
.card-container .card-box:nth-child(2):before,
.card-container .card-box:nth-child(2):after {
    background: linear-gradient(315deg, #03a9f4, #ff0058);
}
.card-container .card-box:nth-child(3):before,
.card-container .card-box:nth-child(3):after {
    background: linear-gradient(315deg, #4dff03, #00d0ff);
}
.card-container .card-box span {
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 2;
    pointer-events: none;
}
.card-container .card-box span::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: 8px;
    background: rgba(255, 255, 255, 0.1);
    -webkit-backdrop-filter: blur(10px);
            backdrop-filter: blur(10px);
    opacity: 0;
    transition: 0.5s;
    animation: animate 2s ease-in-out linear infinite;
    box-shadow: 0 5px 15px rgba(0, 0,0,0.05);
    z-index: 1;
}
.card-container .card-box:hover span::before {
    top: -50px;
    left: 50px;
    width: 100px;
    height: 100px;
    opacity: 1;
}
.card-container .card-box span::after {
    content: '';
    position: absolute;
    bottom: 0;
    right: 0;
    width: 100%;
    height: 100%;
    border-radius: 8px;
    background: rgba(255, 255, 255, 0.1);
    -webkit-backdrop-filter: blur(10px);
            backdrop-filter: blur(10px);
    opacity: 0;
    transition: 0.5s;
    animation: animate 2s ease-in-out linear infinite;
    animation-delay: -1s;
    box-shadow: 0 5px 15px rgba(0, 0,0,0.05);
    z-index: 1;
}
.card-container .card-box:hover span::after {
    bottom: -50px;
    right: 50px;
    width: 100px;
    height: 100px;
    opacity: 1;
}

@keyframes animate {
    0%, 100% {
        transform: translateY(10px);
    }
    50% {
        transform: translateY(-10px);
    }
}
.card-container .card-box .card-content {
    position: relative;
    left: 0;
    padding: 20px 40px;
    background: rgba(0, 0, 0, 0.1);
    box-shadow: 0 5px 15px rgba(0,0,0,0.1);
    border-radius: 8px;
    -webkit-backdrop-filter: blur(10px);
            backdrop-filter: blur(10px);
    z-index: 1;
    transition: 0.5s;
    color: #fff;
}
.card-container .card-box:hover .card-content {
    left: -25px;
    padding: 60px 40px;
}
.card-container .card-box .card-content h2 {
